<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>商品添加</span>
      </div>
      <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
        <div class="shop">
          <el-form-item label="商品名称">
            <el-input class="shopinput" v-model="input" placeholder="请输入内容"></el-input>
          </el-form-item>
        </div>
        <!-- 商品分类 -->
        <div class="shop">
          <el-form-item label="商品分类">
            <el-select v-model="value" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.cateName"
                :value="item.cateName"
              ></el-option>
            </el-select>
          </el-form-item>
        </div>
        <!-- 商品价格 -->
        <div class="shop">
          <el-form-item label="商品价格">
            <el-input-number v-model="num" @change="handleChange" :min="1" :max="10" label="描述文字"></el-input-number>
          </el-form-item>
        </div>
        <!-- 商品图片 -->
        <div class="shop">
          <el-form-item label="商品图片">
            <div class="update">
              <el-upload
              id="box"
                class="avatar-uploader"
                action="http://127.0.0.1:5000/goods/goods_img_upload"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload"
              >
                <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </div>
          </el-form-item>
        </div>
        <!-- 商品描述 -->
        <div class="shop">
          <el-form-item label="商品描述">
            <el-input
              class="shopinput"
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4}"
              placeholder="请输入内容"
              v-model="textarea2"
            ></el-input>
          </el-form-item>
          <!-- 添加商品 -->
          <el-form-item>
            <el-button @click="addShops" type="primary">添加商品</el-button>
          </el-form-item>
        </div>
      </el-form>
    </el-card>
  </div>
</template>

<script>
// 引入接口
import { API_GET_ALL_SHOP_CLASSIFY, API_ADD_SHOPS } from "@/API/apis.js";
export default {
  data() {
    return {
      // 商品图片地址
      imageUrl: "",
      // 上传图地址
      imgUrl:'',
      data: { id: localStorage.id },
      formLabelAlign: {
        name: "",
        region: "",
        type: ""
      },
      // form表单对齐方式
      labelPosition: "right",
      // 商品描述
      textarea2: "",
      dialogImageUrl: "",
      dialogVisible: false,
      // 商品价格
      num: 1,
      // 商品名称
      input: "",
      // 商品分类
      options: [],
      value: ""
    };
  },
  methods: {
    // 商品价格计数器
    handleChange(value) {
      console.log(value);
    },
    // 添加商品
    addShops() {
      // console.log(this.imageUrl,this.value,)
      // 获取商品信息
      // console.log(
      //   this.input,
      //   this.textarea2,
      //   this.num,
      //   this.value,
      //   this.imageUrl
      // );
      // 添加商品
      API_ADD_SHOPS(
        this.input,
        this.value,
        this.num,
        this.imgUrl,
        this.textarea2
      ).then(res => {
        console.log(res);
        //添加商品成功
        if (res.data.code == 0) {
          // 清空输入框
          this.input = "";
          this.textarea2 = "";
          this.num = "";
          this.value = "";
          this.imageUrl = "";
          // 提示添加成功
          this.$message({
            message: "商品添加成功",
            type: "success",
            duration: 1500
          });
          // 提示添加失败
        } else {
          this.$message({
            message: "添加失败",
            type: "error",
            duration: 1500
          });
        }
      });
    },
    // 头像上传成功
    handleAvatarSuccess(res,file) {
      this.imageUrl = URL.createObjectURL(file.raw)
      console.log(res);
      this.imgUrl=res.imgUrl
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    }
  },
  created() {
    // 发送请求获取商品分类
    API_GET_ALL_SHOP_CLASSIFY().then(res => {
      //  结构获取商品分类
      let { categories } = res.data;
      // 遍历商品分类
      categories.forEach((item, index) => {
        // 添加value属性，值为索引
        item.value = index;
        // 添加到当前项中
        this.options.push(item);
      });
    });
  }
};
</script>

<style lang="less" scoped>
.box-card{
  opacity:0.8;
  background-color: rgb(255, 253, 253);
}
.shop {
  margin-bottom: 20px;
}
.shopinput {
  width: 300px;
}
.uodate {
  display: inline-block;
}
#box {
  height: 100px;
  width: 100px;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
#box:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
}
.avatar {
  width: 100px;
  height: 100px;
  display: block;
}
</style>